/*ngdialog-theme-fall-in*/
@keyframes ngdialog-theme-fall-in {
  0% {
    opacity: 0;
    transform: translateZ(600px) rotateX(20deg);
  }
  100% {
    opacity: 1;
    transform: translateZ(0px) rotateX(0deg);
  }
}
@keyframes ngdialog-theme-fall-out {
  0% {
    opacity: 1;
    transform: translateZ(0px) rotateX(0deg);
  }
  100% {
    opacity: 0;
    transform: translateZ(600px) rotateX(20deg);
  }
}
.ngdialog.ngdialog-theme-fall {
  perspective: 1300px;
}
.ngdialog.ngdialog-theme-fall .ngdialog-content {
  transform-style: preserve-3d;
  animation: ngdialog-theme-fall-in 0.3s;
}
.ngdialog-theme-fall.ngdialog-closing .ngdialog-content {
  animation: ngdialog-theme-fall-out 0.5s;
}
.ngdialog-theme-fall.ngdialog-closing .ngdialog-overlay {
  animation: ngdialog-fadeout 0.5s;
}
